* {
  margin: 0;
  padding: 0;
  overflow: visible;
  box-sizing: border-box;
}

.wrapper {
  width: 1200px;
  margin: 0 auto;
  padding: 0 22px;
}

.img {
  background: url(../img/Fon.jpg) no-repeat;
  background-size: cover;
  min-height: 100vh;
}

header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 48px;
  min-height: 100px;
}

header .center {
  display: inline-block;
  min-width: 30px;
  max-width: 100%;
}

header .logotype {
  margin-right: 100%;
  margin-right: calc(100% - 760px);
  width: 107px;
  font-weight: bold;
}

header .btn {
  position: relative;
  text-decoration: #97c89a;
  color: black;
  margin: 0 0 6px 20px;
  font-weight: bold;
}

header .btn:hover:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border-bottom: 1px solid #97c89a;
}

header .btn:nth-child(1) {
  padding: 0;
}

header .btn:nth-child(5) {
  margin: 0 0 0 53px;
}

.container {
  padding-top: 184px;
}

.gradient {
  background: linear-gradient(to top right, #267ebe, #6dd5ed);
}

.main {
  width: 1200px;
  margin: 0 auto;
}

.main_colortext {
  color: #ffffff;
  margin-bottom: 80px;
  font-size: 60px;
}

.clearfix::before,
.clearfix::after {
  content: '';
  display: table;
  clear: both;
}

.feedback_card {
  float: left;
  width: 372px;
  height: 510px;
  background-color: #ffffff;
  border-radius: 10px;
  margin-bottom: 45px;
}

.feedback_card + .feedback_card {
  margin-left: 20px;
}

.feedback_card:nth-child(4) {
  margin-left: 0;
}

.feedback_card_face {
  margin: 68px 0 0 28px;
}

.feedback_card_text {
  margin-left: 33px;
}

.feedback_card_text1_1 {
  margin: 24px 40px 0 35px;
  font-weight: 500;
  margin-bottom: 36px;
}

.feedback_card_text1_2 {
  margin-left: 33px;
}